@import '../App.scss';

.gamelist-container{
    .gamelist-header{
        padding-bottom: $cont-gap;
        // border-top: 1px solid #eee;
        // margin-top: 30px;
        h1{
            height:0.5em;
        }
    }
    .gamelist-games{
        display: flex;
        flex-wrap: wrap;
        .game-box{
            @extend %common-trans;
            width:calc(25% - 30px);
            padding-top: $regular-pad;
            padding-bottom: $regular-pad;
            background:$background1;
            box-shadow: 0 1px 3px rgba(25,25,25,.1);
            text-align: center;
            margin-right:30px;
            margin-bottom:30px;
            .game-schedule{
                margin-bottom: $cont-gap;
                color:$primary;
                padding-bottom: $regular-pad;
                color: #4c84ff;
                font-weight: bold;
                border-bottom: 1px solid #eee;
            }
            .game-team-container{
                position:relative;
            }
            .game-team{
                display: inline-block;
                width:50%;
                .team-title{
                    font-weight: bold;
                }
                .team-standing{
                    padding-top:2px;
                    color: $textColor2;
                    padding-bottom:$regular-pad;
                    font-size: 0.8em;
                }
                .team-logo{
                    padding-bottom:$regular-pad;
                    img{
                        width:80px;
                    }      
                }
                .team-score{
                    font-weight: bold;
                    padding-bottom: $cont-gap;
                    &.winner{
                        color: $primary;
                    }
                }
            }
            .game-not-started{
                position: absolute;
                bottom: 0px;
                width: 100%;
                background: #fcfcfc;
                border-top: 1px solid #eee;
                padding: 5px 0px;
                color: #aaa;
            }
            .game-live{
                bottom: 0px;
                width: 100%;
                background: #f5f5f5;
                border-top: 1px solid #eee;
                padding: 5px 0px;
                color: #f00;
                font-weight: bold;
            }
            .game-detail{
                font-size:0.9em;
                color:#aaa;
                .detail-season{
                    border-top:1px solid #eee;
                    padding-top: 10px;
                    font-weight: bold;
                }
                .detail-arena{
                    font-size:0.8em;
                }
            }
            &:hover{
                cursor: pointer;
                @extend %shadow-hover;       
                @extend %common-trans;

            }
            &:active{
                background-color:$background2;
            }
        }     
    }
}

@media (max-width:1300px){
    .gamelist-container{
        .gamelist-games{
            .game-box{
                &:nth-child(odd){
                    width:calc(50% - 15px);
                    margin-right: 15px;
                }
                &:nth-child(even){
                    width:calc(50% - 15px);
                    margin-right: 0px;
                    margin-left: 15px;
                }
            }
        }
    }   
}

@media (max-width:600px){
    .gamelist-container{
        .gamelist-games{
            .game-box{
                width:100% !important;
                margin-right:0px !important;
                margin-left:0px !important;
            }
        }
    }   
}