@import '../App.scss';
.boxscore-container{
    @extend %common-trans;
    width:100%;
    padding-top: $regular-pad;
    padding-bottom: $regular-pad;
    background:$background1;
    // box-shadow: 0 1px 3px rgba(25,25,25,.1);
    text-align: center;
    margin-right:30px;
    margin-bottom:60px;
    margin-top:$cont-gap;
    position: relative;
    @extend %shadow-hover;
    .boxscore-arena{
        color: $textColor1;
        font-weight: bold;
        padding-top: $regular-pad;
    }
    .boxscore-schedule{
        margin-bottom: 45px;
        color:$textColor2;
        padding-bottom: $cont-gap;
        font-weight: bold;
        border-bottom: 1px solid #eee;
        .boxscore-live{
            bottom: 0px;
            width: 100%;
            color: #f00;
            font-weight: bold;
        }
    }
    .boxscore-team-container{
        position:relative;
    }
    .boxscore-team{
        display: inline-block;
        width:50%;
        .team-title{
            font-weight: bold;
            font-size:1.5em;
        }
        .team-standing{
            padding-top:2px;
            color: $textColor2;
            padding-bottom:$regular-pad;
            font-size: 1.2em;
        }
        .team-logo{
            padding-bottom:$regular-pad;
            img{
                width:100px;
            }      
        }
        .team-score{
            font-weight: bold;
            padding-bottom: $cont-gap;
            font-size:1.5em;
            &.winner{
                color: $primary;
            }
        }
    }
    .boxscore-period{
        position: relative;
        padding:$cont-gap;
        .period-row{
            display:flex;
            flex-wrap: wrap;
            div{
                width:20%;
                display:inline-block;
                font-weight: bold;
                &.period-quarter{
                    padding-bottom:$regular-pad;
                    padding-top:$regular-pad;
                    border-bottom:1px solid #eee;
                    border-top:1px solid #eee;
                    margin-bottom:15px;
                }
                &.period-team{
                    margin-bottom:$regular-pad;

                    &.head{
                        border-bottom:1px solid #eee;
                        border-top:1px solid #eee;
                        padding-top:$regular-pad;
                    }
                }
            }
        }
        .playbyplay-container{
            padding-top:45px;
            width:100%;
            .period-instruct{
                padding-bottom:30px;
                color: $textColor2;
            }
            .period-container{
                display:flex;
                flex-wrap: wrap;
                .period{
                    &.pbp{
                        color:$primary;
                        background: #f8f8f8;
                        border-bottom: none;
                    }
                    &:first-child{
                        border-left:1px solid #eee;
                    }
                    &:hover{
                        background:#f8f8f8;
                        @extend %common-trans;
                    }

                    width:25%;
                    font-weight: bold;
                    border:1px solid #eee;
                    border-left:none;
                    box-sizing: border-box;
                    padding: $regular-pad;
                }
            }

            .playbyplay-button{
                width:100%;
                margin-bottom:$cont-gap;
                button{
                    background:$primary;
                    border:1px solid $primary;
                    color:#fff;
                    font-weight: bold;
                    padding:$regular-pad $cont-gap;
                    margin-left:7px;
                    margin-right:7px;
                    &:hover{
                        cursor: pointer;
                        opacity:0.8;
                        @extend %common-trans;
                    }
                    &:active{
                        opacity:1;
                    }
                    &.close{
                        background: #fff;
                        border: 1px solid #1c63ff;
                        color: #1c63ff;
                    }
                }
            }

            .playbyplay-list{
                width:100%;
                padding:15px;
                box-sizing: border-box;
                background:#f8f8f8;
                border: 1px solid #eee;
                border-top: none;
                .play{
                    background:#fff;
                    width:100%;
                    padding:$regular-pad;
                    display:flex;
                    flex-wrap: wrap;
                    box-sizing: border-box;
                    box-shadow: 0 1px 3px rgba(25, 25, 25, 0.1);
                    /* border: 1px solid #eee; */
                    margin-top: 15px;
                    &:first-child{
                        margin-top:0px;
                    }
                    &:hover{
                        background:#f6f9ff;
                        cursor:default;
                    }
                    .play-time{
                        width:100%;
                        display:flex;
                        flex-wrap: wrap;
                        font-weight: bold;
                        .time-time{
                            width:100%;
                            color:$primary;
                            font-size: 1.3em;
                        }
                        .time-score{
                            width:100%;
                        }
                        .play-description{
                            width:100%;
                            font-weight: bold;
                        }
                        .play-logo{
                            width:100%;
                            img{
                                width:50px;
                                margin: auto;
                                display: block;
                            }
                        }
                    }
                }
            }

        }
    }
    .boxscore-not-started{
        position: absolute;
        bottom: -15px;
        width: 100%;
        background: #fcfcfc;
        border-top: 1px solid #eee;
        padding: 10px 0px;
        color: #aaa;
    }

    &:hover{
        cursor: pointer;
        @extend %shadow-hover;       
        @extend %common-trans;

    }
    &:active{
        background-color:$background2;
    }
}    

@media (min-width:1200px){
    .boxscore-container{
        font-size:1.2em;
        .boxscore-team .team-logo img
        {
            width:150px;
        }
    }    

}