$primary: #1c63ff;
$primary2: #feaa4d;

$background1: #ffffff;
$background2: #f5f5f5;

$textColor1: #222222;
$textColor2: #8d8f9b;

$regular-pad: 15px;
$cont-gap: 30px;

%common-trans{
    -webkit-transition: all linear 0.4s;
	transition: all linear 0.4s;  
}
%shadow-hover{
    -webkit-box-shadow: 0 1px 20px rgba(76,132,255,0.3);
    -moz-box-shadow: 0 1px 20px rgba(76,132,255,0.3);
    box-shadow: 0 1px 20px rgba(76,132,255,0.3);
}

body{
    background: $background2;
    font-size: 15px !important;
    color: $textColor1;
}