@import '../App.scss';

.header-container{
    width:100%;
    background:#fff;
    header{
        display:flex;
        flex-wrap: wrap;
        padding-bottom: 15px;
        padding-top: 15px;
        .logo{
            width:30%;
            text-align: left;
            // display:none;
            img{
                width:100px;
            }
            span{
                font-weight: bold;
                color: $textColor1;
                position: relative;
                top:-12px;
                font-size: 1.2em;
            }
        }
    }
    
}

.parent-container{
    width:80%;
    margin: auto;
    padding: $regular-pad;
    margin-bottom: $cont-gap;
    .game-content{
        width:100%;
        position: relative;
        .date-picker{
            margin-top:$cont-gap;
            margin-bottom: $cont-gap;
            width:100%;
            text-align: right;

            .selected-title{
                display: inline;
                float: left;
                font-weight: bold;
                h1{
                    margin:0px;
                }
            }
            .selected-date-container{
                padding-right: 30px;
            }
            input{
                padding: 10px;
                border: none;
                background-color: #fff;
                border-radius: 0px !important;
                border: 1px solid #eee;
                font-size: 1.0em;
                color:$primary;
                font-weight: bold;
                width: 150px;
             }
            span{
                color: $textColor2;
                font-weight: bold;
                background: #fff;
                padding: 8.5px 10px;
                border: 1px solid #eee;
                border-right: none;
            }
        }        
        .game-nofound{
            color: $textColor2;
            padding-top:$cont-gap;
            font-weight: bold;
        }
    }
}
.loading-container{
    margin-top: $cont-gap;
    position: relative;
    height:50px;
    div{
        left:50%;
        transform: translateX(-50%);
        position: absolute;
    }
}

.footer{
    text-align: center;
    padding: $regular-pad;
    font-size:0.8em;
    margin-bottom: $regular-pad;
    color:$textColor2;
}
@media (max-width:1300px){
    .parent-container{
        .game-content{
            .date-picker{
                .selected-date-container{
                     padding-right: 0px !important;
                }
            }        
        }
    }
}
@media (max-width:700px){
    .parent-container{
        .game-content{
            .date-picker{
                text-align: left !important;
                .selected-title{
                    display: block !important;
                    float:auto !important;
                    float:unset !important;
                    margin-bottom: $regular-pad;
                }
            }        
        }
    }
}